// 封装 axios 请求模块

import axios from 'axios'
import store from '@/store'
import JSONBig from 'json-bigint'
import router from '@/router'
import { Notify } from 'vant'

// JSONBig 可以处理数据中超出 JavaScript 安全整数范围的问题
// JSONBig.parse()
// JSONBig.stringify()

const request = axios.create({
  baseURL: 'http://ttapi.research.itcast.cn/', // 接口的基准路径
  // baseURL: 'http://toutiao-app.itheima.net/',

  // 自定义后端返回的原始数据
  // data: 后端返回的原始数据，即 JSON 格式的字符串
  transformResponse: [function (data) {
    try {
      // 如果转换成功则返回转换的数据结果
      return JSONBig.parse(data)
    } catch (err) {
      // 如果转换失败，则包装为统一数据格式并返回
      return data
    }

    // axios 内部默认处理后端返回数据
    // return JSON.parse(data)
  }]
})

// 请求拦截器
// Add a request interceptor
request.interceptors.request.use(function (config) {
  // 请求发起会经过这里
  // config ：本次请求的配置对象
  const { user } = store.state
  if (user && user.token) {
    config.headers.Authorization = `Bearer ${user.token}`
  }
  // 注意：这里务必返回 config 配置对象，否则请求就停在这里出不去了
  return config
}, function (error) {
  // 如果请求出错（还没有发出去）会进入这里
  return Promise.reject(error)
})

// 响应拦截器
request.interceptors.response.use(
  // 响应成功进入第一个函数
  // 该函数的参数是响应对象
  function (response) {
    // 任何位于2xx范围内的状态代码都会触发此函数
    // 处理响应数据
    return response
  },
  // 响应失败进入第二个函数，该函数的参数是错误对象
  async function (error) {
    // 任何不在2xx范围内的状态码都会触发此函数
    // 处理响应错误
    // 如果响应码是 401，则请求获取最新的 token
    // console.dir(error) // console.dir()可以显示一个对象的所有属性和方法
    if (error.response && error.response.status === 401) {
      // 校验是否有 refresh_token
      const user = store.state.user
      if (!user || !user.refresh_token) {
        return redirectLogin() // 跳转到不同的url，但这个方法会向history栈添加一个记录，点击后退会返回到上一个页面。

        // router.replace('/login') // 同样是跳转到指定的url，但是这个方法不会向history里面添加新的记录，点击返回，会跳转到上上一个页面。上一个记录是不存在的。
      }
      // 如果 refresh_token，则请求获取新的 token
      try {
        // 注意这里不要用之前的 request 发请求，避免又 401 造成死循环
        const res = await axios({
          method: 'PUT',
          url: 'http://ttapi.research.itcast.cn/app/v1_0/authorizations',
          headers: {
            Authorization: `Bearer ${user.refresh_token}`
          }
        })
        // 如果获取成功，则把新的 token 更新到容器中
        // console.log('刷新 token 成功', res)
        store.commit('setUser', {
          token: res.data.data.token, // 最新获取的可用 token
          refresh_token: user.refresh_token // 还是原来的 refresh_token
        })
        // 把之前失败的用户请求继续发出去
        // config 是一个对象，其中包括本次失败请求相关的那些配置信息，如：url、method 都有
        // return 把 request 的请求结果继续返回给发请求的具体位置
        return request(error.config)
      } catch (err) {
        // 如果获取失败，直接跳转登录页
        // console.log('请求刷新 token 失败', err)
        redirectLogin()
      }
    } else if (error.response.status === 500) {
      Notify('服务端异常，请稍后重试')
    }
    return Promise.reject(error)
  }
)

// 跳转至登录页函数
function redirectLogin() {
  router.replace({
    name: 'login',
    // 传递查询参数，查询参数以 ？ 作为分隔符放到 url 后面
    query: {
      // redirect 为自定义数据名
      // 当前路由对应的路由信息对象 router.currentRoute 与组件中的 this.$route 一样
      redirect: router.currentRoute.fullPath
    }
  })
}

export default request
